body{
    min-width: 320px;
}

.fade-enter {
    opacity: 0.00;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 100ms ease-in;
}
.fade-exit {
    opacity: 0;
}

.fade-exit.fade-exit-active {
    opacity: 0.0;
    transition: opacity 100ms ease-in;
}


/*end of transition css*/
/*end of transition css*/

.inline-block{
    display: inline-block;
}

.content-container{
    min-height: 70vh;
}

button{
    outline: none!important;
}
ul{
    padding-left: 0px;
}
ul,li{
    list-style: none;
}

.loading-container{
    width: 100%;
    text-align: center;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: calc(100vh - 150px);
}
.main-div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 700px;

}

.fade-enter {
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
}

.right{
    float: right;
}
.left{
    float: left;
}
h1{
    margin-bottom: 20px;
}
.intl-tel-input{
    width:100%;
}

.intl-tel-input .flag-container{
    position: relative;
    height: 45px;
    margin-bottom: 10px;
    /*margin-top: 10px;*/
}

.intl-tel-input.allow-dropdown .selected-flag {
    background-color: rgba(0,0,0,.05);

    width: 100%;
}
.country-name-main{
    display: inline-block;
    width: 100%;
    padding-left: 32px!important;
    margin-top: 8px;
    border: none!important;
    z-index: -1!important;
    position: relative;
    /* margin-top: 22px; */
    padding-top: 7px;
    box-shadow: none!important;
    background: transparent;

}

.intl-tel-input input[type=tel]{
    border-radius: 5px;
    /*padding: 10px!important;*/
    padding: 12px 14px!important;
    font-size: 16px;
    height: auto;
    outline: none!important;
    border: 1px solid #c5c5c5;


}

form.invalid .intl-tel-input input[type=tel]{
    border:1px solid red;
}

.btn-toolbar{
    text-align: center;
    margin-top:20px;
}
.btn-toolbar .btn{
    float:none;
}

form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: relative;
}

.country-selected-container{
    height: 0px;
    position: relative;
}

.country-selected{
    position: absolute;
    top: auto;
    bottom: -33px;
    width: 100%;
    left: 0px;
    padding-top: 9px;
    padding-left: 36px;
}

.sign-up-form{
    margin-bottom: 10px;
    padding: 12px 14px;
    font-size: 16px;
    height: auto;
}

.input-errors{
    color: red;
    margin-bottom: 8px;
    margin-top: -7px;
    display: block;
}
.intl-tel-input input#phonenumber {
    margin-bottom: 10px !important;
    width: calc(100% - 86px);
    float: right;
}
.no-horizantal{
    padding: 0px;
}

.inline-row-flex{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    
}

